if (document.querySelector('.burger')) {
  function burgerMenu() {
    const burger = document.querySelector('.burger')
    const menu = document.querySelector('.nav__menu')
    const body = document.querySelector('body')
    burger.addEventListener('click', () => {
      if (!menu.classList.contains('active')) {
        menu.classList.add('active')
        burger.classList.add('active')
        body.classList.add('locked')
      } else {
        menu.classList.remove('active')
        burger.classList.remove('active')
        body.classList.remove('locked')
      }
    })
  }
  burgerMenu();
}
//
if (document.querySelector('input[type="range"]')) {
  const rangeInput = document.querySelector('input[type="range"]')
  function handleInputChange(e) {
    e.preventDefault()
    const count = document.querySelector('#USDT')
    const rangeSum = document.querySelector('#sum')
    const output = document.querySelector('#output')
    var rangeCount = 900000
    const persents = document.querySelectorAll('input[name="persent"]')
    var target = e.target
    if (e.target.type !== 'range') {
      target = document.getElementById('range')
    }
    const min = target.min
    const max = target.max
    var val = target.value
    target.style.backgroundSize = (val - min) * 100 / (max - min) + '% 100%'
    var sum = target.value / 20533.8809
    count.innerHTML = sum.toFixed(2)
    rangeSum.innerHTML = rangeCount - target.value
    output.innerHTML = target.value
    for (var i = 0; i < persents.length; i++) {
      const persent = persents[i]
      persent.addEventListener('click', () => {
        val = persent.value
        target.style.backgroundSize = (val - min) * 100 / (max - min) + '% 100%'
        sum = persent.value / 20533.8809
        count.innerHTML = sum.toFixed(2)
        rangeSum.innerHTML = rangeCount - persent.value
        output.innerHTML = persent.value
      })
    }
  }
  rangeInput.addEventListener('input', handleInputChange)
  rangeInput.addEventListener('touchmove', handleInputChange)
  window.addEventListener('touchstart', handleInputChange)
  window.addEventListener('load', handleInputChange)
}
